/* App.scss */
.App {
  text-align: center;

  .App-header {
    // background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    // color: white;

    .container {
      // background-color: aquamarine;
      padding: 10px;
      .title-author-container {
        flex-direction: column;
        justify-content:left ;
        p {
          font-size: 32px;
          overflow-wrap: break-word;
          white-space: nowrap; /* Add this line */
          text-overflow: ellipsis; /* Add this line */
          max-width: 80%;
        }
        small {
          a {
            text-decoration: none;
            font-size: 16px;
          }
        }
      }

      .image-buttons-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 10px;
        img {
          width: 500px;
          height: 500px;
        }
        button {
          margin: 5px;
        }
        .buttons-wrapper {
          display: flex;
          align-items: center;
          justify-content: space-between;
          width: 100%;

          #change-meme {
            height: 500px;
            border: none;
            text-align: center;
            align-items: center;
            padding: 25px;

            font-size: 25px;

            background-color: transparent;
          }
          #change-meme:hover {
            background-color: #d2d2d2;
            cursor: pointer;
          }
        }
      }
    }
  }
}
